import React from 'react';
import { BrowserRouter, Routes,Route } from 'react-router-dom';
import Layout from './components/Layout';
import './App.scss';
import routes from './routes';

function App() {
	return (
		<BrowserRouter>
			<Layout>
				<Routes>
					{routes.map((i, k)=><Route key={k} path={i.path} element={i.element} />)}
				</Routes>	
			</Layout>
		</BrowserRouter>
	);
}

export default App;
