import React from 'react';
import { Link } from 'react-router-dom';
import './layout.scss';
import useStore from '../../useStore';
import bird from '../../assets/img/download.png';
// import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
// import { solid, regular, brands, icon } from '@fortawesome/fontawesome-svg-core/import.macro'

interface LayoutProps {
	children: React.ReactNode
}

const Layout = ({children}: LayoutProps) => {
	const footerData1 = ['Gigs', 'Jobs', 'Freelancers', 'Blog', 'Skills'];
	const footerData2 = ['Premium', 'Job', 'mining', 'About Us', 'Terms of use', 'Privacy policy', 'FAQ'];
	const [toggleMenu, setToggleMenu] = React.useState(false);
	const [screenWidth, setScreenWidth] = React.useState(window.innerWidth);

	const [subMenuShow, setSubMenuShow] = React.useState(false);
	const [lang, setLang] = React.useState('english');

	const onMenuClick = () => {
		setSubMenuShow(!subMenuShow);
	}

	const toggleNav = () => {
		setToggleMenu(!toggleMenu)
	}

	React.useEffect(() => {
		const changeWidth = () => {
			setScreenWidth(window.innerWidth);
		}

		window.addEventListener('resize', changeWidth)

		return () => {
			window.removeEventListener('resize', changeWidth)
		}

	}, [])

	const {theme, user, T, update} = useStore();

	const onLang = (lang: string) => {
		if (lang === 'en-US') {
			setLang('english');
		} else {
			setLang('chinese');
		}
		update({lang})
	}

	return (
		<div className={`main ${theme==='night' ? 'night' : ''}`}>
			<header className='container'>
				<div className='big-header'>
					<div className='d justify'>
						<div className='d middle gap'>
							<Link to="/">
								<h1 style={{fontSize: 24, fontWeight: 900}} className="d middle gap-sm">
									<img src="/logo.svg" alt="logo" width="32" height="32" />
									DEAMJOB 
										
									<div>
									<sup style={{fontSize: 12, backgroundColor: 'red', color: 'white', padding: '0 5px 2px', borderRadius: 6}}>IN PROGRESS</sup>
									</div>
									
								</h1>
							</Link>
							<ul>
								<li><Link to="/jobs">{T('menu.jobs')}</Link></li>
								<li><Link to="/gigs">{T('menu.gigs')}</Link></li>
								<li><Link to="/full-time-jobs">{T('menu.fulltime')}</Link></li>
								<li><Link to="/blog">{T('menu.blog')}</Link></li>
							</ul>
						</div>
						<div>
							<ul>
								{user!==undefined ? (
									<>
										<li><Link to="dashboard">Dashboard</Link></li>
										<li><Link to="Logout">Logout</Link></li>
									</>
								) : (
									<>
										<li><Link to="login">
											<button className='secondary'>Login</button>
										</Link></li>
										<li><Link to="register">
											<button className='outline'>Register</button>
										</Link></li>
									</>
								)}
								{
									theme === 'night' ? (
										<li>
											<button className='theme-btn' onClick={()=>update({theme: undefined})}>
												<i className="fa-regular fa-user icon"> </i>
											</button> 
										</li>
									) : (
										<li>
											<div className='theme-btn' onClick={()=>update({theme: 'night'})}>
												Night
											</div>
										</li>
									)
								}
								<li><button className='lang relative' onClick={onMenuClick}>Languages
								{
									subMenuShow && (
										<div className='dropdown'>
											<p className={`item ${lang === 'english' ? 'active' : ''}`} onClick={()=>onLang('en-US')}>English</p>
											<p className={`item ${lang === 'chinese' ? 'active' : ''}`} onClick={()=>onLang('zh-CN')}>中国语（简体）</p>
										</div>
									)
								}
								</button></li>
							</ul>
						</div>
					</div>
				</div>
				<div className='small-header'>
					<div className='d justify'>
						<button onClick={toggleNav} className='sym-group'>
								<div className='btn-sym'></div>
								<div className='btn-sym'></div>
						</button>
						{(toggleMenu || screenWidth > 1120) && (
							<div className='list'>
								<Link to="/jobs"><h1 className='header-h1'>{T('menu.jobs')}</h1></Link>
								<Link to="/gigs"><h1 className='header-h1'>{T('menu.gigs')}</h1></Link>
								<Link to="/full-time-jobs"><h1 className='header-h1'>{T('menu.fulltime')}</h1></Link>
								<Link to="/blog"><h1 className='header-h1'>{T('menu.blog')}</h1></Link>
								<div className='m2 d center' style={{width:'100%'}}>
									<Link to="login" style={{margin:'1em'}}>
										<button className='secondary'>Login</button>
									</Link>
									<Link to="register" style={{margin:'1em'}}>
										<button className='outline'>Register</button>
									</Link>
								</div>
							</div>
						)}
					</div>
				</div>
			</header>
			{children}
			<footer className='container'>
				<div className='container'>
					<div className='d justify'>
						<div className='col4 col4-md d p2'>
							<h1 style={{color:'white'}}>DeamJob</h1>
							<p style={{color:'gray'}}>The LaborX® word mark is a registered trademark owned by LaborX PTY LTD U.S. 
							Certificate of Registration No. 6,098,830IP Australia Trade Mark Number 1960088</p>
						</div>
						<div className='col7 col7-md d-row p2' style={{justifyContent:'space-around'}}>
							<div className='col4'>
								{
									footerData1.map((i,k) => (
										<p key={k} style={{color:'white'}}>{i}</p>
									))
								}
							</div>
							<div className='col4'>
								{
									footerData2.map((i,k) => (
										<p key={k} style={{color:'white'}}>{i}</p>
									))
								}
							</div>
							<div className='col4 d-column' style={{justifyContent:'space-between'}}>
								<div>
									<p style={{color:'white'}}>Contact us</p>
									<p style={{color:'white',fontSize:'23px'}}>info@laborx.com</p>
								</div>
								<div className='d-row' style={{width:'100%',justifyContent:'space-around'}}>
									<img src={bird} alt="bird" className='footer-icon' />
								</div>
							</div>
						</div>
					</div>
				</div>
			</footer>
		</div>
	)
}

export default Layout;